.lui-hover-box-overlay {
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: fixed;
  transform: translate(0);
  left: 0;
  top: 0;
  transition: opacity 0.2s ease, background 0.2s ease;
}
.lui-hover-box-overlay .lui-hover-box {
  overflow: hidden;
  margin: 0;
  position: absolute;
  border-radius: 5.625px;
}
.lui-hover-box-overlay .lui-hover-box-scroll {
  overflow-y: scroll;
}
.lui-hover-box-overlay .lui-hover-box-bar {
  z-index: 2;
  border-radius: 5.625px;
  position: absolute;
  transform: translate(-50%, -50%);
}
.lui-hover-box-overlay.lui-visible {
  pointer-events: all;
}
.lui-hover-box-overlay .lui-hover-box-flat {
  margin: 0;
  position: absolute;
  border-radius: 5.625px;
}
