body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-size: 20px;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  box-sizing: border-box;
}
* {
  box-sizing: border-box;
}
.lui-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.lui-section {
  position: relative;
  padding: 15px;
  flex-shrink: 0;
  width: -webkit-fill-available;
}
.lui-noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}
.lui-section-content {
  margin: 11.25px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.lui-section-content p {
  width: 100%;
  margin-top: 8;
  margin-bottom: 8;
}
.lui-section-content .lui-btn,
.lui-section-content .lui-bar-btn {
  width: inherit;
}
.lui-absolute-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.lui-section-title {
  margin: 0;
  margin-top: 0;
  margin-left: 0;
  width: 100%;
  /* padding-right: 30; */
  text-transform: capitalize;
  display: inline-flex;
  align-items: center;
  font-size: 20px;
  flex-wrap: wrap;
  font-weight: 700;
}
.lui-section-title-bar {
  min-height: 11.25px;
  width: 90px;
  border-radius: 5.625px;
  margin-left: 11.25px;
  margin-top: 3.33333333px;
}
.lui-alert-dot {
  border-radius: 50%;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0;
  right: 0;
}
a {
  text-decoration: none;
}
.lui-input-bar {
  transition: inherit;
  height: 30px;
  width: 11.25px;
  flex-shrink: 0;
  margin: 0 11.25px;
  border-radius: 5.625px;
}
.lui-input-full-w .lui-input-wrap {
  width: 100%;
}
.lui-input-full-w input {
  width: 100%;
}
.lui-hint-label {
  position: absolute;
  height: 100%;
  width: auto;
  top: 0;
  right: 0;
}
.lui-overlay-input {
  position: absolute !important;
  left: 0;
  height: 100%;
  display: flex;
  align-items: center;
  top: 0;
}
.lui-input-wrap {
  position: relative;
  width: -webkit-fill-available;
}
.lui-modal-shadow {
  box-shadow: 0px 0px 11.25px #00000014;
}
.lui-btn {
  -moz-appearance: none;
  -webkit-appearance: none;
  white-space: pre;
  font-family: "monor", monospace;
  max-width: 100%;
  outline: none;
  border: none;
  padding: 0 11.25px;
  min-height: 45px;
  min-width: 45px;
  margin: 5.625px;
  display: inline-flex;
  align-items: center;
  position: relative;
  justify-content: flex-start;
  border-radius: 5.625px;
  font-weight: 400;
  transition: filter 0.3s ease, background 0.3s ease, color 0.3s ease;
  cursor: default;
}
.lui-btn.lui-trans_fixed {
  transform: translate(0);
}
.lui-btn textarea {
  border: none;
  color: inherit;
  background: none;
  padding: 3px 5.625px;
  min-width: 100%;
  width: 100%;
  min-height: 100px;
  outline: none;
}
.lui-btn .lui-chip {
  margin-top: 0;
}
.lui-btn .lui-label {
  max-width: 100%;
  white-space: initial;
  flex-shrink: 0;
  height: auto;
  vertical-align: middle;
  line-height: normal;
  margin: 5.625px;
}
.lui-btn .lui-top-label {
  transition: opacity 0.3s ease;
  opacity: 1;
  padding: 0;
  margin: 0;
  position: absolute;
  left: 16.875px;
  top: 8.4375px;
  font-size: 20px;
}
.lui-btn .lui-label-opaque {
  opacity: 0.5;
}
.lui-btn .lui-top-label.lui-label-opaque {
