}
.lui-btn input,
.lui-btn .lui-overlay-input {
  margin: 0 5.625px !important;
}
.lui-btn input {
  padding: 0;
}
.lui-btn input,
.lui-btn select,
.lui-btn .lui-input {
  -webkit-appearance: none;
  width: 100%;
  user-select: all;
  outline: none;
  background: none;
  border: none;
  color: inherit;
  vertical-align: middle;
  line-height: normal;
  position: relative;
  min-width: 100px;
}
.lui-btn input.lui-hidden,
.lui-btn select.lui-hidden,
.lui-btn .lui-input.lui-hidden {
  min-width: 0px;
  height: 0px;
}
.lui-btn input[type="time"],
.lui-btn select[type="time"],
.lui-btn .lui-input[type="time"],
.lui-btn input[type="date"],
.lui-btn select[type="date"],
.lui-btn .lui-input[type="date"] {
  user-select: none;
}
.lui-btn input[type="file"],
.lui-btn select[type="file"],
.lui-btn .lui-input[type="file"] {
  width: 100%;
  /* visibility: hidden; */
  height: 100% !important;
  left: 0;
  top: 0;
  position: absolute;
  opacity: 0;
  z-index: 1;
  -webkit-appearance: none;
}
.lui-btn input[type="range"],
.lui-btn select[type="range"],
.lui-btn .lui-input[type="range"] {
  border-radius: 5.625px;
  height: 5.625px;
}
.lui-btn input[type="range"]::-webkit-slider-thumb,
.lui-btn select[type="range"]::-webkit-slider-thumb,
.lui-btn .lui-input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  /* Override default look */
  appearance: none;
  background: white;
  border: 3px solid black;
  cursor: grab;
  height: 22.5px;
  width: 22.5px;
  border-radius: 11.25px;
}
.lui-btn select {
  min-width: auto;
  cursor: pointer;
  height: 45px;
  padding: 0 11.25px;
}
.lui-btn .lui-label-2 {
  padding-right: 30px;
  opacity: 0.5;
}
.lui-btn .lui-overlay-icon {
  transition: opacity 0.3s ease;
  position: absolute;
  right: 0;
  top: 0;
  opacity: 0.5;
  font-size: 22.5px;
  padding: 8px;
}
.lui-btn ::placeholder {
  color: inherit;
  opacity: 0.5;
}
.lui-btn.lui-top-label-btn {
  padding-top: 45px;
  padding-bottom: 11.25px;
}
pre {
  font-family: "monor", monospace;
  font-size: inherit;
}
.lui-hidden {
  opacity: 0 !important;
  width: 0 !important;
  margin: 0 !important;
}
.lui-chip {
  height: 30px;
  border-radius: 5.625px;
  margin: 0 5.625px;
  padding: 0 11.25px;
  font-family: "monor", monospace, monospace;
  display: inline-flex;
  flex-shrink: 0;
  font-size: 11px;
  align-items: center;
  justify-content: center;
}
.lui-checkbox-circle {
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin: 0 5.625px;
  margin-right: 11.25px;
  overflow: hidden;
  z-index: 1;
  transform: translate(0);
  transition: background 0.3s ease, transform 0.3s ease, opacity 0.3s ease;
}
.lui-checkbox-circle-inner {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  transform: scale(0.6);
  transition: background 0.3s ease, transform 0.3s ease, opacity 0.3s ease;
}
.lui-checkbox-circle-inner.lui-active {
  transform: scale(0.7);
}
.lui-toggle {
  flex-shrink: 0;
  transform: translate(0);
  border-radius: 5.625px;
  height: 30px !important;
  width: 45px !important;
  margin-right: 11.25px;
  margin-left: 5.625px;
}
.lui-toggle i {
  font-size: 26.66666667px !important;
}
.lui-toggle .lui-toggle-on {
