.lui-tab-wrapper,
.lui-tab-content {
  position: relative;
  display: flex;
  width: auto;
  flex-shrink: 0;
}
.lui-menu-bar {
  position: absolute;
  width: fit-content;
  min-width: max-content;
  transform: translate(0, 0);
  display: flex;
}
.lui-overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity 0.3s ease, background 0.3s ease;
  pointer-events: none;
}
.lui-overlay.lui-overlay-hidden {
  opacity: 0;
}
.lui-overlay .lui-overlay-slide {
  width: 100%;
  height: 100%;
}
.lui-overlay.lui-overlay-children-pointer-events * {
  pointer-events: all;
}
.lui-overlay-blocking {
  pointer-events: all;
}
.lui-overlay-alert {
  padding: 11.25px;
  font-size: 11px;
  font-family: "monor", monospace;
}
.lui-loader-wrapper {
  width: auto;
  height: auto;
}
.lui-loader {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  width: 16.875px;
  height: 16.875px;
  border-radius: 5.625px;
  background-color: #FFFFFF;
  animation: lui-_ii_rotate 1s infinite ease-in-out;
  transition: opacity 0.3s ease;
}
.lui-loader-stop {
  animation: lui-_ii_rotate 0.3s ease-in-out;
  animation-iteration-count: 0;
  opacity: 0.2;
  transition: opacity 0.3s ease-in-out, transform 0.1s ease-in-out;
}
@keyframes lui-_ii_rotate {
  0% {
    transform: perspective(30px) rotateX(0deg) rotateY(0deg);
  }
  50% {
    transform: perspective(30px) rotateX(-180deg) rotateY(0deg);
  }
  100% {
    transform: perspective(30px) rotateX(-180deg) rotateY(-180deg);
  }
}
